<template>
  <div class="frame-container mine-page">
    <div class="mime-info-first">
      <div class="vm-bg-img"></div>
      <div class="info-message-box">
        <span class="name">{{info.name}}</span>
        <span class="tel">{{info.phone}}</span>
        <span class="tage">{{info.type}}</span>
        <span class="edit-password" @click="resetPsw">
          <i class="el-icon-edit"></i>修改密码
        </span>
      </div>
      <!--      <div class="info-first-container center-flex">-->
      <!--        <div>-->
      <!--          <p><span v-if="userInfo.userType === 2">一级代理商</span>-->
      <!--             <span v-if="userInfo.userType === 3">二级代理商</span>-->
      <!--          </p>-->
      <!--        </div>-->
      <!--      </div>-->
    </div>
    <div class="mime-info-second">
      <ul>
        <li class="clearfix">
          <p class="fl">加入时间</p>
          <p class="fr time">{{info.createTime}}</p>
        </li>
        <li class="clearfix">
          <p class="fl">提现到支付宝</p>
          <p class="fr" v-if="!isName">
            请完善支付宝信息
            <i @click="editPay" class="el-icon-arrow-right"></i>
          </p>
           <p class="fr genxin" v-else>
            {{name}}
            <i @click="editPay" class="el-icon-arrow-right"></i>
          </p>
        </li>
        <!-- <li class='genxin'>
          <p>版本更新</p>
          <p class="genxin"><span style="color:red;">新版本·</span><i class="el-icon-arrow-right" style="color:#cccccc"></i></p>
        </li> -->
        <!-- <template v-if="userType===2 || userType===3 || userType===4 ">
          <li class="clearfix">
            <p class="fl">微信分润</p>
            <p class="fr">{{info.wxPayProrata | toPercent}}</p>
          </li>
          <li class="clearfix">
            <p class="fl">支付宝分润</p>
            <p class="fr">{{info.zfbPayProrata | toPercent}}</p>
          </li>
          <li class="clearfix">
            <p class="fl">随行付分润</p>
            <p class="fr">{{info.sxfPayProrata | toPercent}}</p>
          </li>
          <li class="clearfix">
            <p class="fl">富友分润</p>
            <p class="fr">{{info.fyPayProrata | toPercent}}</p>
          </li>
        </template>-->
        <!-- <li v-for="(item,i) in secondInfoList"
            :key="`mine_${i}`"
            class="clearfix">
          <p class="fl">{{item.name}}</p>
          <p class="fr">{{item.value}}</p>
        </li>-->
      </ul>
    </div>
    <div class="out-login">
      <el-button icon="icon-other-tuichu" type="primary" @click="loginOut">退出登录</el-button>
      <!--      <p @click="loginOut">退出登录</p>-->
    </div>
    <div class="shade" v-show="editMessage"></div>
    <div class="edit-pay-contain" v-show="editMessage">
      <p class="edit-title">支付宝信息</p>
      <el-form class="edit-from" label-position="right" label-width="60px">
        <el-form-item label="姓名:">
          <el-input v-model="name" placeholder="请填写真实姓名"></el-input>
        </el-form-item>
        <el-form-item label="支付宝:">
          <el-input v-model="aliAccountNumber" placeholder="请填写支付宝账号"></el-input>
        </el-form-item>
        <el-form-item class="mg0">
          <el-button @click="cancelEdit">取消</el-button>
          <el-button type="primary" @click="submitPay">确定</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { loginApi } from "@/api";
import { loginInfoLocal, afterLoginInfoLocal } from "@/storage";
import * as types from "@/router/types";
export default {
  mixins: [],
  data() {
    return {
      secondInfoList: [
        { name: "手机", value: "" },
        { name: "加入时间", value: "" },
        { name: "抽佣比例", value: "" }
      ],
      userId: afterLoginInfoLocal.getJSON().userId,
      info: {},
      userInfo: {},
      userType: "",
      editMessage: false,
      name: "",
      aliAccountNumber: "",
      info_id:'',
      isName:false
    };
  },
  created() {
    this.getUserInfo();
    this.getPay();
    console.log(this.userId,"userid")
  },
  filters: {
    toPercent: function(val) {
      console.log("1");
      let percent = val || "0";
      return percent * 100 + "%";
    }
  },
  methods: {
    /**
     * 获取用户信息
     * */
    getUserInfo() {
      let userInfo = afterLoginInfoLocal.getJSON();
      let params = {
        companyId: userInfo.companyId
      };
      this.userType = userInfo.userType;
      loginApi.getUserInfo(params).then(res => {
        this.userInfo = res.obj;
        let newData = Object.assign({}, res.obj),
          type = "";
        this.info = newData;
        if (newData.type == 1) {
          type = "服务商";
        } else if (newData.type == 2) {
          type = "一级代理";
        } else if (newData.type == 3) {
          type = "二级代理";
        } else if (newData.type == 4) {
          type = "三级代理";
        } else if (newData.type == -1) {
          type = "超级服务商";
        } else {
          type = "普通";
        }
        this.info.type = type;
        this.secondInfoList.forEach(item => {
          if (item.name === "手机") {
            this.$set(item, "value", newData.phone);
          }
          if (item.name === "加入时间") {
            this.$set(item, "value", newData.createTime);
          }
          if (item.name === "抽佣比例") {
            this.$set(item, "value", newData.payProrata * 100 + "%");
          }
        });
      });
    },
    /**
     * 完善支付宝信息
     */
    editPay() {
      this.editMessage = true;
    },
    // 取消编辑
    cancelEdit() {
      this.editMessage = false;
    },
    // 提交编辑的信息
    submitPay() {
      loginApi
        .addAlipay(this.userId,this.name,this.aliAccountNumber )
        .then(
          res => {
            this.editMessage = false;
            this.$toast.message(res.msg)
            this.getPay();
          },
          err => {
            this.$toast.error(err.msg);
          }
        );

    },
     // 获取信息
    getPay() {
      loginApi
        .getAlipay(this.userId)
        .then(
          res => {
            this.name = res.obj.aliName;
            this.aliAccountNumber = res.obj.aliAccountNumber;
            if(this.name!='' && this.aliAccountNumber!=''){
                this.isName = true;
            }
          },
          err => {
            this.$toast.error(err.msg);
          }
        );
    },
    /**
     * 退出登录
     */
    loginOut() {
      this.$confirm("确定退出登录", "提示").then(res => {
        if (res.result) {
          loginInfoLocal.remove();
          afterLoginInfoLocal.remove();
          sessionStorage.clear();
          this.$router.push({ name: types.LOGIN });
        }
      });
    },
    /**
     * 修改密码
     */
    resetPsw() {
      this.$router.push({ name: types.RESETPASSWORD });
    }
  }
};
</script>
<style scoped>
.genxin{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
</style>


